.header {
   position: absolute;
   top: 0;
   width: 100%;
   z-index: 50;
   &__menu {
      display: flex;
      flex-direction: column;
   }
   &__submenu {
      position: relative;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--black-color);
      display: flex;
      transition: all 0.3s;
      cursor: pointer;
      &.active {
         .header__submenu__list {
            width: 150px;
            padding: 10px 10px;
            gap: 8px;
            box-shadow: 0px 4px 30px rgba(17, 17, 52, 0.12);
            border-radius: 10px;
         }
         .header__phone__number-img {
               transform: rotate(180deg);
         }
      }
      &__list {
         position: absolute;
         top: 20px;
         left: 0;
         background-color: var(--light-color);
         z-index: 100;
         width: 0;
         padding: 0;
         margin: 0;
         overflow: hidden;
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         justify-content: center;
         list-style-type: none;
         &-link {
            a {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 16px;
               line-height: 130%;
               color: var(--black-color);
               &:hover {
                  color: var(--green-color);
                  text-decoration: underline;
               }
            }
         }
      }
      span {
         margin-left: 8px;
         display: flex;
         align-items: center;
      }
      &:hover {
         color: var(--green-color);
         text-decoration: underline;
      }
   }
   &__value {
      position: fixed;
      padding-top: 20px;
      margin-left: auto;
      margin-right: auto;
      width: 100vw;
      padding-bottom: 10px;
      background-color: var(--light-color);
      z-index: 1000;
      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
      }
   }
   &__phone {
      display: flex;
      align-items: center;
   }
   &__logo {
      margin-right: 124px;
   }
   &__location {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-right: 30px;
      &-text {
         max-width: 124px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 12px;
         line-height: 130%;
         color: rgba(48, 48, 48, 0.6);
         transition: all 0.3s;
         &:hover {
            color: var(--green-color);
            text-decoration: underline;
         }
      }
   }
   &__time {
      display: flex;
      align-items: center;
      gap: 10px;
      margin-right: 80px;
      &-text {
         max-width: 146px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 12px;
         line-height: 130%;
         color: rgba(48, 48, 48, 0.6);
      }
   }
   &__phone {
      display: flex;
      gap: 16px;
      &__number {
         max-width: 183px;
         text-align: center;
         padding-top: 7px;
         &-link {
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 20px;
            line-height: 130%;
            color: var(--black-color);
            transition: all 0.3s;
            &:hover {
               color: var(--green-color);
               text-decoration: underline;
            }
         }
         &-text {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 12px;
            line-height: 130%;
            color: rgba(48, 48, 48, 0.6);
            margin: 0;
         }
      }
      &__btn {
         display: flex;
         flex-direction: row;
         justify-content: center;
         align-items: center;
         gap: 10px;
         width: 215px;
         height: 58px;
         border: 1.5px solid var(--green-color);
         font-family: 'Gilroy';
         font-weight: 500;
         font-size: 16px;
         line-height: 130%;
         color: var(--green-color);
         transition: all 0.3s;
         &:hover {
            background-color: var(--green-color);
            color: var(--light-color);
         }
      }
   }
   &__nav {
      margin-top: 109px;
      &__items {
         list-style: none;
         display: flex;
         flex-direction: row;
         justify-content: space-between;
         align-items: flex-start;
         margin: 0;
         padding: 0;
         margin-top: 31px;
         gap: 35px;
      }
      &__item {
         position: relative;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--black-color);
         display: flex;
         transition: all 0.3s;
         &:hover {
            color: var(--green-color);
            text-decoration: underline;
         }
      }
      &-burger {
         position: relative;
         display: none;
      }
   }
}

.burger { @include burger }


@media (max-width: 1200px) {
   .container {
      padding: 0 10px;
   }
   .header__value {
      align-items: center;
   }
   .header__location {
      display: none;
   }
   .header__time {
      display: none;
   }
   .header__phone__number {
      display: none;
   }
   .header__nav__items {
      display: none;
   }
   .header__nav-burger {
      display: flex;
      z-index: 100;
   }
}

