.blog {
   padding: 80px 0;
   background-color: var(--main-color);
   h2.title {
      color: var(--light-color);
   }
   &__wrapper {
      display: flex;
      gap: 30px;
   }
   &__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 243px;
      height: 58px;
      background-color: var(--green-color);
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--light-color);
      transition: all 0.3s;
      &:hover {
         background-color: var(--light-color);
         color: var(--green-color);
         border: 2px solid var(--green-color);
      }
      &-wrapper {
         margin-top: 30px;
         width: 100%;
         display: flex;
         justify-content: center;
      }
   }
   &__item {
      height: 380px;
      width: 364px;
      position: relative;
      &-1 {
         background: url(../img/blog1.jpg) center center/cover no-repeat;
      }
      &-2 {
         background: url(../img/blog2.jpg) center center/cover no-repeat;
      }
      &-3 {
         background: url(../img/blog3.jpg) center center/cover no-repeat;
      }
      &__wrapper {
         position: absolute;
         bottom: 0;
         left: 0;
         background-color: var(--light-color);
         height: 155px;
         width: 100%;
         &-title {
            margin: 25px 31px 18px 20px;
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 24px;
            line-height: 110%;
            color: var(--black-color);
         }
         &-link {
            margin-left: 20px;
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            text-decoration-line: underline;
            color: var(--green-color);
            transition: all 0.3s;
            &:hover {
               color: var(--black-color);
            }
         }
      }
   }
}

@media (max-width: 1200px) {
   .blog {
      &__wrapper {
         flex-wrap: wrap;
         justify-content: center;
      }
   }
}

@media (max-width: 992px) {
   
