.comments {
   padding-bottom: 80px;
   h2.title {
      margin-bottom: 30px;
   }
   &__wrapper {
      display: flex;
      gap: 12px;
   }
   &__subcomment {
      margin-top: 35px;
      margin-left: 59px;
      margin-bottom: 35px;
   }
   &__comment {
      &__item {
         background: #F7F8FA;
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         gap: 8px;
         &__send {
            background-color: var(--light-color);
            border-left: 4px solid #54B435;
            box-shadow: 0px 1px 4px rgba(160, 181, 195, 0.2);
            border-radius: 0px 8px 8px 0px;
            margin: 15px 24px 0px;
            &-descr {
               padding: 0 20px;
               padding-bottom: 8px;
            }
            &-name {
               font-weight: 600;
               font-size: 16px;
               line-height: 120%;
               padding-top: 8px;
               padding-left: 20px;
            }
         }
         &-name {
            padding-top: 20px;
            padding-left: 24px;
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 16px;
            line-height: 120%;
            color: #43375D;
            span {
               margin-left: 8px;
               background: #E8EAEF;
               border-radius: 15px;
               padding: 6px 10px 7px;
               color: #929BAA;
            }
         }
         &-descr {
            padding: 0 24px;
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 150%;
            color: #43375D;
         }
         &__menu {
            margin-top: 24px;
            width: 100%;
            background-color: var(--light-color);
            display: flex;
            gap: 12px;
            padding-top: 10px;
            &-time {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 14px;
               line-height: 130%;
               text-align: right;
               color: #929BAA;
            }
            &-changed {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 14px;
               line-height: 130%;
               text-align: right;
               color: #929BAA;
            }
            &-send {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 14px;
               line-height: 130%;
               text-align: right;
               color: #11283C;
            }
         }
      }
   }
}

@media (max-width: 1200px) {
   .comments {
      &__img {
         min-width: 40px;
         img {
            width: 40px;
            height: 40px;
         }
      }
   }
}

