.works {
   padding: 80px 0;
   &__wrapper {
      display: flex;
      gap: 45px;
      h2.title {
         width: 365px;
         margin-bottom: 30px;
      }
   }
   &__btn {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      width: 339px;
      height: 58px;
      background-color: var(--green-color);
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--light-color);
      transition: all 0.3s;
      &:hover {
         background-color: var(--light-color);
         color: var(--green-color);
         border: 2px solid var(--green-color);
      }
   }
   &__list {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(3, auto);
      column-gap: 30px;
      row-gap: 40px;
      &__item {
         &-title {
            font-family: 'Gilroy';
            font-weight: 600;
            font-size: 24px;
            line-height: 130%;
            color: var(--black-color);
            margin-bottom: 20px;
         }
         &-descr {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: var(--black-color);
         }
      }
   }
}

@media (max-width: 1200px) {
   .works {
      &__wrapper {
         flex-wrap: wrap;
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .works {
      padding: 30px 0;
      &__list {
         display: flex;
         flex-wrap: wrap;
         &__item {
            &-title {
               font-size: 18px;
            }
            &-descr {
               font-size: 14px;
            }
         }
      }
   }
}

