/* stylelint-disable */

@mixin burger {
  --burger-width: 30px;
  --burger-height: 20px;
  --burger-line-height: 2px;

  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
  z-index: 100;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    z-index: 100;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
  }

  &__lines {
   position: absolute;
   top: 49px;
   gap: 8px;
   width: 100vw;
   right: 0;
   display: none;
   flex-direction: column;
   align-items: center;
   z-index: 100;
   background-color: var(--light-color);
   &.active {
      display: flex;
   }
  }

  &__line {
   display: flex;
   flex-direction: column;
   align-items: center;
   a {
      color: var(--black-color)
   }
   div {
      display: flex;
      align-items: center;
   }
    transition: transform 0.3s ease-in-out;
    span {
      margin-left: 8px;
      display: flex;
      align-items: center;
    }
    &.active {
      .burger__submenu__list {
         display: flex;
      }
      .header__phone__number-img {
         transform: rotate(180deg);
      }
    }
  }

  &__submenu {
   &__list {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: center;
      list-style-type: none;
      margin: 0;
      padding: 0;
      padding-top: 5px;
      gap: 8px;
      display: none;
      &-link {
         a {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            color: var(--black-color);
         }
      }
   }
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__lines {
      display: flex;

    }

    .burger__line {
      transition: transform 0.3s ease-in-out;
      margin-bottom: 10px;
      position: relative;
      z-index: 1000;
      a {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--black-color);
         &:hover {
            color: var(--green-color);
            text-decoration: underline;
         }
      }
    }
  }
}
