.abouth {
   padding: 80px 0;
   background: url(../img/abouth.jpg) center center/cover no-repeat;
   h2.title {
      margin-bottom: 45px;
   }
   &__wrapper {
      display: flex;
      justify-content: space-between;
   }
   &__item {
      display: flex;
      flex-direction: column;
   }
   &__list {
      display: flex;
      margin-top: 50px;
      max-width: 485px;
      justify-content: space-between;
      &-item {
         display: flex;
         flex-direction: column;
         &-number {
            position: relative;
            font-family: 'Gilroy';
            font-weight: 700;
            font-size: 56px;
            line-height: 130%;
            color: var(--green-color);
            &::after {
               content: '';
               position: absolute;
               height: 1px;
               width: 82px;
               left: 0;
               bottom: 4px;
               background-color: var(--green-color);
            }
         }
         &-descr {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 14px;
            line-height: 140%;
            color: var(--black-color);
         }
      }
   }
   &__text {
      max-width: 688px;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--main-color);
      margin: 0;
   }
   &__link {
      max-width: 175px;
      font-family: 'Gilroy';
      font-weight: 600;
      font-size: 14px;
      line-height: 140%;
      color: var(--green-color);
      margin-bottom: 6px;
      transition: all 0.3s;
      &:hover {
         color: var(--black-color);
         text-decoration: underline;
      }
      &-descr {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 14px;
         line-height: 140%;
         color: var(--black-color);
         margin-bottom: 25px;
         &:last-child {
            margin-bottom: 0;
         }
      }
   }
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .abouth {
      &__item {
         &-2 {
            flex-direction: row;
            gap: 5px;
            flex-wrap: wrap;
         }
      }
      &__wrapper {
         flex-direction: column;
         gap: 30px;
      }
      &__text {
         font-size: 14px;
      }
      &__list {
         &-item {
            &-number {
               font-size: 36px;
            }
            &-descr {
               font-size: 12px;
            }
         }
      }
   }
}

