.maintenance {
   width: 277px;
   background-color: var(--main-color);
   padding: 20px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   &__title {
      font-family: 'Gilroy';
      font-weight: 600;
      font-size: 20px;
      line-height: 130%;
      color: var(--light-color);
      margin-bottom: 15px;
   }
   &__list {
      list-style-type: none;
      margin: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      gap: 8px;
      &-item {
         position: relative;
         padding-left: 24px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: rgba(255, 255, 255, 0.7);
         transition: all 0.3s;
         &:hover {
            color: var(--light-color);
            text-decoration: underline;
         }
         &::before {
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            content: '';
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: var(--green-color);
         }
      }
   }
}