.accreditation {
   padding: 80px 0;
   &__wrapper {
      display: flex;
      justify-content: space-between;
   }
   &__text {
      max-width: 585px;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: rgba(48, 48, 48, 0.7);
   }
   &__swiper {
      position: relative;
      width: 585px;
      padding: 0 21px;
      &-next {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         right: 34px;
         background-image: url(../img/svg/next.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
      }
      &-prev {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         left: 14px;
         background-image: url(../img/svg/prev.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
      }
   }
   &__slide {
      img {
         width: 140px;
      }
   }
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .accreditation {
      &__wrapper {
         flex-direction: column;
         justify-content: center;
      }
      &__text {
         margin-right: auto;
         margin-left: auto;
      }
      &__swiper {
         width: 100%;
      }
   }
}

@media (max-width: 767px) {

}

@media (max-width: 576px) {
   .accreditation {
      &__swiper {
         &-next {
            right: 15px;
         }
         &-prev {
            left: 15px;
         }
      }
   }
}

