.hero-services {
   padding-top: 242px;
   padding-bottom: 80px;
   h2.title {
      font-family: 'Gilroy';
      font-weight: 700;
      font-size: 48px;
      line-height: 130%;
      text-transform: uppercase;
      margin-bottom: 0;
   }
   &__wrapper {
      display: flex;
      justify-content: space-between;
   }
   &__services {
      background: #F7F7F7;
      padding: 40px 24px 40px 40px;
      width: 277px;
      &-title {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 24px;
         line-height: 140%;
         color: #000000;
      }
      &-list {
         margin: 0;
         margin-top: 25px;
         padding: 0;
         list-style-type: none;
         display: flex;
         flex-direction: column;
         gap: 12px;
      }
      &-item {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: rgba(48, 48, 48, 0.7);
         cursor: pointer;
         &:hover {
            color: var(--green-color);
            text-decoration: underline;
         }
      }
   }
   &__subtitle {
      margin-top: 15px;
      margin-bottom: 35px;
      font-family: 'Gilroy';
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      color: #000000;
   }
   &__application {
      font-family: 'Gilroy';
      font-style: normal;
      font-weight: 600;
      font-size: 24px;
      line-height: 140%;
      color: #000000;
      margin-bottom: 20px;
   }
   &__form {
      display: flex;
      flex-wrap: wrap;
      gap: 23px;
      input {
         background: var(--light-color);
         border: 1px solid #E8E8E8;
         width: 250px;
         height: 58px;
         padding: 10px 10px 10px 24px;
         &::placeholder {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            color: var(--main-color);
         }
      }
      .application__form-text {
         margin-left: 33px;
         max-width: 100%;
      }
      .application__form__btn {
         width: 243px;
      }
   }
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .hero-services {
      &__item {
         width: 100%;
      }
      h2.title {
         font-size: 18px;
      }
      &__subtitle {
         font-size: 16px;
      }
      &__application {
         font-size: 16px;
      }
      &__wrapper {
         flex-direction: column;
         align-items: center;
      }
      &__services {
         width: 100%;
         display: flex;
         flex-direction: column;
         align-items: center;
         &-title {
            font-size: 16px;
         }
         &-item {
            font-size: 14px;
         }
         &-list {
            align-items: center;
         }
      }
   }
}

