.article {
   padding: 80px 0;
   &__wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 39px;
      h2.title {
         margin-bottom: 20px;
      }
   }
   &__text {
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--black-color);
      margin: 0;
      margin-bottom: 30px;
      &:last-child {
         margin-bottom: 0;
      }
   }
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .article {
      padding: 30px 0;
      &__wrapper {
         display: flex;
         flex-direction: column;
      }
      &__text {
         font-size: 12px;
         margin-bottom: 10px;
      }
   }
}

