.hero-article-blog {
   &__title {
      font-family: 'Gilroy';
      font-weight: 700;
      font-size: 32px;
      line-height: 130%;
      color: var(--black-color);
      margin: 0;
      margin-bottom: 25px;
   }
   &__subtitle {
      font-family: 'Gilroy';
      font-weight: 700;
      font-size: 22px;
      line-height: 130%;
      color: var(--black-color);
      margin-top: 25px;
      margin-bottom: 15px;
   }
   &__list {
      list-style-type: none;
      margin-bottom: 30px;
      display: flex;
      flex-direction: column;
      gap: 10px;
      &-item {
         margin: 0;
         padding: 0;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: var(--black-color);
         position: relative;
         padding-left: 24px;
         &::before {
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            content: '';
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: var(--green-color);
         }
      }
   }
   &__text {
      margin: 0;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--black-color);
      &-wrap {
         max-width: 483px;
      }
   }
   &__item {
      display: flex;
      flex-direction: column;
      gap: 50px;
   }
   &__wrapper {
      display: flex;
      gap: 30px;
      &:first-child {
         gap: 0;
      }
      &:nth-child(4) {
         gap: 0;
      }
      &-1 {
         flex-direction: column;
      }
   }
}

@media (max-width: 1200px) {
   .hero-article-blog {
      &__wrapper {
         justify-content: space-between;
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .hero-article-blog {
      &__title {
         font-size: 18px;
      }
      &__wrapper {
         align-items: center;
         flex-direction: column;
      }
   }
}

