.mini-sertificats {
   &__wrapper {
      display: flex;
      justify-content: space-between;
      margin-bottom: 12px;
      &__btns {
         display: flex;
         gap: 10px;
      }
   }
   &__item {
      background-color: var(--main-color);
      padding: 20px;
      padding-bottom: 0;
      width: 277px;
   }
   &__swiper-slide {
      img {
         height: 157px;
      }
   }
   &__swiper {
      &-next {
         height: 24px;
         width: 24px;
         background-image: url(../img/svg/next.svg);
         background-position: center center;
         overflow: visible;
         z-index: 99;
      }
      &-prev {
         height: 24px;
         width: 24px;
         background-image: url(../img/svg/prev.svg);
         background-position: center center;
         overflow: visible;
         z-index: 99;
      }
   }
}
h3.title {
   font-family: 'Gilroy';
   font-weight: 600;
   font-size: 20px;
   line-height: 130%;
   color: var(--black-color);
   margin: 0;
}

.mini-partners {
