.height {
   padding: 80px 0;
   h2.title {
      text-align: center;
      margin-bottom: 44px;
   }
   &__wrapper {
      display: flex;
      gap: 21px;
   }
   &__item {
      &-number {
         font-family: 'Gilroy';
         font-weight: 700;
         font-size: 32px;
         line-height: 130%;
         text-align: center;
         color: var(--green-color);
         margin: 0;
         margin-bottom: 15px;
      }
      &-descr {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         text-align: center;
         color: var(--black-color);
         margin: 0;
      }
   }
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .height {
      padding: 30px 0;
      &__wrapper {
         flex-wrap: wrap;
         justify-content: center;
      }
   }
}

