.services {
   padding: 80px 0;
   background: #FAFAFA;
   h2.title {
      max-width: 406px;
   }
   &__wrapper {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      gap: 30px;
   }
   &__item {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 507px;
      padding: 30px 36px 40px 40px;
      background-color: var(--light-color);
      &__title {
         font-family: 'Gilroy';
         font-weight: 700;
         font-size: 24px;
         line-height: 130%;
         color: var(--black-color);
         margin-top: 17px;
         margin-bottom: 27px;
      }
      &__list {
         list-style-type: none;
         padding: 0;
         margin: 0;
         display: flex;
         flex-direction: column;
         gap: 8px;
         width: 100%;
         &-item {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: rgba(48, 48, 48, 0.7);
            &::before {
               content: url('../img/svg/servicesArrow.svg');
               width: 12px;
               height: 7.5px;
               margin-right: 16px;
            }
         }
      }
      &__btn {
         position: absolute;
         bottom: 40px;
         font-family: 'Gilroy';
         font-weight: 500;
         font-size: 16px;
         line-height: 130%;
         color: var(--green-color);
         width: 240px;
         height: 58px;
         border: 2px solid var(--green-color);
         display: flex;
         flex-direction: row;
         justify-content: center;
         align-items: center;
         transition: all 0.3s;
         &:hover {
            background-color: var(--green-color);
            color: var(--light-color);
         }
      }
   }
}

@media (max-width: 1200px) {
   .container {
      padding: 0 10px;
   }
   .services {
      &__wrapper {
         grid-template-columns: repeat(2, 1fr);
         grid-template-rows: repeat(3, 1fr);
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .services {
      &__wrapper {
         grid-template-columns: repeat(1, 1fr);
         grid-template-rows: repeat(6, 1fr);
         padding: 0 30px;
      }
      &__item__btn {
         font-size: 14px;
      }
      &__item__list {
         width: 250px;
         &-item {
            font-size: 14px;
         }
      }
   }
}

@media (max-width: 576px) {
   .services__wrapper {
      padding: 0 10px;
   }
}

