.hero {
   background: url('../img/mainpage.jpg') top left/cover no-repeat;
   padding-top: 280px;
   padding-bottom: 185px;
   .container {
      position: relative;
   }
   &__title {
      max-width: 660px;
      padding-left: 5px;
      font-family: 'Gilroy';
      font-weight: 300;
      font-size: 48px;
      line-height: 130%;
      text-transform: uppercase;
      color: var(--black-color);
      span {
         font-weight: 700;
      }
   }
   &__img {
      position: absolute;
      bottom: -190px;
      right: 44px;
      z-index: 1;
   }
   &__btn {
      width: 214px;
      height: 58px;
      padding: 21px 45px;
      font-family: 'Gilroy';
      font-weight: 500;
      font-size: 16px;
      line-height: 130%;
      color: var(--light-color);
      background: var(--green-color);
      transition: all 0.3s;
      &:hover {
         background-color: var(--light-color);
         color: var(--green-color);
         border: 2px solid var(--green-color);
      }
   }
}

@media (max-width: 1200px) {
   .container {
      padding: 0 10px;
   }
   .hero {
      &__img {
         display: none;
      }
      &__title {
         max-width: 80%;
         font-size: 34px;
         margin-bottom: 55px;
      }
   }
}

