.hero-blog {
   padding-top: 212px;
   padding-bottom: 80px;
   &__wrapper {
      display: flex;
      gap: 29px;
   }
   &__list {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(3, 1fr);
      gap: 30px;
      &__item {
         width: 278px;
         height: 364px;
         position: relative;
         &-1 {
            background: url(../img/heroBlog1.jpg) center center/cover no-repeat;
         }
         &-2 {
            background: url(../img/heroBlog2.jpg) center center/cover no-repeat;
         }
         &-3 {
            background: url(../img/heroBlog3.jpg) center center/cover no-repeat;
         }
         &-4 {
            background: url(../img/heroBlog2.jpg) center center/cover no-repeat;
         }
         &-5 {
            background: url(../img/heroBlog3.jpg) center center/cover no-repeat;
         }
         &-6 {
            background: url(../img/heroBlog1.jpg) center center/cover no-repeat;
         }
         &-7 {
            background: url(../img/heroBlog2.jpg) center center/cover no-repeat;
         }
         &-8 {
            background: url(../img/heroBlog1.jpg) center center/cover no-repeat;
         }
         &-9 {
            background: url(../img/heroBlog3.jpg) center center/cover no-repeat;
         }
         &__wrapper {
            position: absolute;
            left: 3px;
            right: 3px;
            bottom: 3px;
            background-color: var(--light-color);
            padding: 25px 16px 39px 20px;
            &-name {
               font-family: 'Gilroy';
               font-weight: 600;
               font-size: 20px;
               line-height: 110%;
               color: var(--black-color);
               margin-bottom: 26px;
            }
            &-link {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 16px;
               line-height: 130%;
               text-decoration-line: underline;
               color: var(--green-color);
               transition: all 0.3s;
               &:hover {
                  color: var(--black-color);
               }
            }
         }
      }
   }
   &__centr {
      display: flex;
      justify-content: center;
      margin-top: 30px;
   }
   &__btn {
      width: 243px;
      height: 58px;
      background-color: var(--green-color);
      display: flex;
      justify-content: center;
      align-items: center;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--light-color);
      transition: all 0.3s;
      &:hover {
         background-color: var(--light-color);
         color: var(--green-color);
         border: 2px solid var(--green-color);
      }
   }
   &__item {
      display: flex;
      flex-direction: column;
      gap: 20px;
   }
}

@media (max-width: 1200px) {
   .hero-blog {
      padding-bottom: 30px;
      &__item {
         display: none;
      }
      &__wrapper {
         justify-content: center;
      }
   }
}

@media (max-width: 992px) {
   .hero-blog {
      &__list {
         display: flex;
         flex-wrap: wrap;
         justify-content: center;
      }
   }
   .hero-blog__list__item__wrapper-name {
      font-size: 16px;
      margin-bottom: 5px;
   }
   .hero-blog__list__item__wrapper-link {
      font-size: 12px;
   }
}

