.achivements {
   padding: 80px 0;
   h2.title {
      max-width: 852px;
      margin-bottom: 20px;
   }
   &__text {
      max-width: 688px;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--black-color);
      margin-bottom: 60px;
   }
   &__swiper {
      position: relative;
      padding: 0 21px;
      &-next {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         right: 0px;
         background-image: url(../img/svg/next.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
      }
      &-prev {
         height: 42px;
         width: 42px;
         position: absolute;
         top: 50%;
         left: 0px;
         background-image: url(../img/svg/prev.svg);
         background-position: center center;
         transform: translateY(-50%);
         overflow: visible;
         z-index: 99;
      }
   }
   &__wrapper {
      position: absolute;
      left: 3px;
      bottom: 8px;
      right: 3px;
      background-color: var(--light-color);
      padding: 25px 49px 25px 20px;
      &-name {
         max-width: 305px;
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 24px;
         line-height: 110%;
         color: var(--black-color);
         margin-bottom: 18px;
      }
      &-descr {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--black-color);
      }
   }
}
.swiper-slide {
   position: relative;
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .achivements {
      padding: 30px 0;
      &__wrapper {
         padding: 10px;
         &-name {
            font-size: 16px;
            margin-bottom: 5px;
         }
         &-descr {
            font-size: 12px;
         }
      }
   }
}

@media (max-width: 767px) {

}

@media (max-width: 576px) {
   .achivements{
      &__wrapper {
         &-name {
            font-size: 12px;
         }
         &-descr {
            font-size: 8px;
         }
      }
   }
}

