.real {
   padding: 80px 0;
   position: relative;
   h2.title {
      max-width: 852px;
      margin-bottom: 15px;
   }
   &__text {
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 24px;
      line-height: 140%;
      color: var(--black-color);
   }
   &__wrapper {
      display: flex;
      align-items: center;
   }
   &__sertificats {
      display: flex;
      &-descr {
         max-width: 411px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: var(--black-color);
         span {
            font-weight: 600;
            color: var(--green-color);
         }
      }
   }
   &__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 265px;
      height: 58px;
      background-color: var(--green-color);
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--light-color);
      transition: all 0.3s;
         &:hover {
            background-color: var(--light-color);
            color: var(--green-color);
            border: 2px solid var(--green-color);
         }
   }
   &__img {
      position: absolute;
      top: 0;
      right: 0;
      z-index: -1;
   }
}

@media (max-width: 1200px) {
   .real {
      &__img {
         display: none;
      }
   }
}

@media (max-width: 992px) {
   .real {
      padding: 30px 0;
      &__text {
         font-size: 16px;
      }
      &__wrapper {
         flex-direction: column;
      }
      &__btn {
         margin-left: auto;
         margin-right: auto;
      }
   }
}

