.advantages {
   padding: 80px 0;
   background-color: var(--main-color);
   h2.title {
      color: var(--light-color);
      max-width: 518px;
   }
   &__wrapper {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 30px;
   }
   &__item {
      padding: 45px;
      padding-right: 72px;
      background-color: var(--light-color);
      width: 482px;
      height: 266px;
      position: relative;
      &-big {
         width: 688px;
      }
      &-title {
         font-family: 'Gilroy';
         font-weight: 700;
         font-size: 32px;
         line-height: 130%;
         color: var(--black-color);
      }
      &-img {
         &-1 {
            position: absolute;
            bottom: 18px;
            right: 0;
         }
         &-2 {
            position: absolute;
            top: 27px;
            right: 92px;
         }
         &-3 {
            position: absolute;
            top: 45px;
            right: 48px;
         }
      }
      &-text {
         margin-top: 20px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: rgba(48, 48, 48, 0.7);
         &-1 {
            max-width: 304px;
         }
         &-3 {
            max-width: 318px;
         }
         &-4 {
            max-width: 304px;
         }
         &-5 {
            max-width: 304px;
         }
         &-6 {
            max-width: 275px;
         }
      }
   }
}

@media (max-width: 1200px) {
   .advantages {
      &__item {
         &-big {
            width: 100%;
         }
         width: 100%;
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .advantages {
      &__item {
         &-title {
            font-size: 18px;
         }
         &-text {
            font-size: 14px;
         }
         &-img {
            &-1 {
               display: none;
            }
            &-2 {
               display: none;
            }
            &-3 {
               display: none;
            }
         }
      }
   }
}

