.search-documentation {
   padding: 80px 0;
   &__wrapper {
      display: flex;
      gap: 25px;
   }
   &__text {
      max-width: 688px;
      margin: 0;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--black-color);
   }
   &__form {
      margin-top: 30px;
      input {
         width: 329px;
         height: 58px;
         border: 1px solid #E8E8E8;
         padding: 19px 24px;
         margin-right: 30px;
         &::placeholder {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            color: var(--main-color);
         }
      }
      &__label {
         input {
            margin-right: 0;
         }
      }
      .application__form__btn {
         margin-top: 20px;
         margin-bottom: 20px;
      }
      .application__form-text {
         margin-left: 33px;
         max-width: 296px;
      }
   }
}

@media (max-width: 1200px) {

}

@media (max-width: 992px) {
   .search-documentation {
      padding: 30px 0;
      &__wrapper {
         flex-direction: column;
         align-items: center;
      }
      &__form {
         input {
            width: 100%;
            margin-bottom: 10px;
         }
      }
   }
}

