.popup {
   position: fixed;
   top: 0;
   left: 0;
   background: rgba(45, 118, 21, 0.5);
   backdrop-filter: blur(5px);
   width: 100vw;
   height: 100vh;
   z-index: 2000;
   display: none;
   &.active {
      display: block;
   }
   &__modal {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      display: flex;
      flex-direction: column;
      align-items: center;
      background: var(--light-color);
      border-radius: 12px;
      max-width: 540px;
      padding: 40px 45px;
   }
   &__title {
      font-family: 'Gilroy';
      font-weight: 700;
      font-size: 32px;
      line-height: 120%;
      color: var(--black-color);
      margin-bottom: 8px;
   }
   &__text {
      max-width: 366px;
      margin: 0;
      padding: 0;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      text-align: center;
      color: var(--black-color);
      margin-bottom: 28px;
   }
   &__form {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 15px;
      input {
         width: 450px;
         height: 58px;
         padding: 10px 10px 10px 24px;
         background: var(--light-color);
         border: 1px solid #E8E8E8;
      }
   }
   .application__form__btn {
      width: 100%;
   }
   &__close {
      position: absolute;
      top: 15px;
      right: 20px;
      cursor: pointer;
   }
}

@media (max-width: 767px) {
   .popup {
      &__form {
         input {
            width: 100%;
            font-size: 14px;
         }
      }
      &__title {
         font-size: 18px;
         text-align: center;
      }
      &__text {
         display: none;
      }
      &__modal {
         padding: 10px;
      }
   }
}