.stages {
   padding: 80px 0;
   h2.title {
      margin-bottom: 60px;
   }
   &__wrapper {
      position: relative;
      hr {
         margin: 0;
         border: none;
         border-top: 1px solid #D9D9D9;
      }
      &::after {
         content: url(../img/svg/Vector\ 32.svg);
         position: absolute;
         top: 43.5px;
         left: -28px;
      }
   }
   &__item {
      padding-top: 30px;
      padding-bottom: 45px;
      display: flex;
      gap: 280px;
      &-number {
         font-family: 'Gilroy';
         font-weight: 700;
         font-size: 24px;
         line-height: 130%;
         color: var(--green-color);
      }
      &__main {
         display: flex;
         flex-direction: column;
         &-title {
            font-family: 'Gilroy';
            font-weight: 700;
            font-size: 24px;
            line-height: 130%;
            color: var(--black-color);
         }
         &-text {
            max-width: 585px;
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: rgba(48, 48, 48, 0.7);
            margin: 0;
            margin-top: 16px;
         }
         &-list {
            list-style-type: none;
            display: flex;
            flex-direction: column;
            gap: 10px;
            margin: 0;
            padding: 0;
            margin-top: 16px;
            &-item {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 16px;
               line-height: 140%;
               color: rgba(48, 48, 48, 0.7);
               position: relative;
               padding-left: 24px;
               &::before {
                  content: '';
                  position: absolute;
                  top: 50%;
                  left: 0;
                  transform: translateY(-50%);
                  width: 8px;
                  height: 8px;
                  border-radius: 50%;
                  background-color: var(--green-color);
               }
            }
         }
      }
   }
}

@media (max-width: 1200px) {
   .stages {
      &__wrapper::after {
         display: none;
      }
      &__item {
         gap: 10px;
         justify-content: space-between;
      }
   }
}

