@media (max-width: 767px) {
   .stages {
      &__item {
         &__main {
            &-title {
               font-size: 18px;
            }
            &-text {
               font-size: 14px;
            }
            &-list-item {
               font-size: 14px;
            }
         }
      }
   }
}

