.calculator {
   padding: 80px 0;
   background: #FAFAFA;
   &__wrapper {
      display: flex;
      gap: 30px;
   }
   &__right {
      background-color: var(--light-color);
      padding: 40px;
      &-text {
         margin: 0;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: var(--black-color);
      }
   }
   &__left {
      background-color: var(--light-color);
      padding: 40px;
      width: 688px;
      &__descr {
         display: flex;
         margin-bottom: 25px;
         &:last-child {
            margin-bottom: 0;
         }
      }
      &__items {
         display: flex;
         justify-content: space-between;
         width: 469px;
      }
      &-title {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 24px;
         line-height: 130%;
         color: var(--black-color);
         margin-bottom: 25px;
      }
      &__list {
         display: flex;
         align-items: center;
         margin-bottom: 30px;
         &__item {
            &-name {
               font-family: 'Gilroy';
               font-weight: 600;
               font-size: 20px;
               line-height: 140%;
               color: var(--black-color);
            }
            &-descr {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 12px;
               line-height: 140%;
               color: var(--black-color);
            }
         }
      }
      &-number {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 42px;
         line-height: 130%;
         color: var(--green-color);
         margin-right: 23px;
      }
   }
}

@media (max-width: 1200px) {
   .calculator {
      &__wrapper {
         flex-direction: column;
         align-items: center;
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .calculator {
      &__left {
         width: 100%;
      }
      &__right {
         width: 100%;
      }
   }
   .calculator__left__list__item-name {
      font-size: 14px;
   }
   .calculator__left__list__item-descr {
      font-size: 10px;
   }
   .calculator__left__items {
      gap: 5px;
   }
}

