.comands {
   padding: 80px 0;
   h2.title {
      text-align: center;
      margin-bottom: 69px;
   }
   &__wrapper {
      display: flex;
      gap: 30px;
      width: 100%;
   }
   &__third {
      display: grid;
      grid-template-areas: "pic1 pic2"
                           "pic3 pic3";
                           gap: 30px;
   }
   &__pic1 {
      grid-area: pic1;
   }
   &__pic2 {
      grid-area: pic2;
   }
   &__pic3 {
      grid-area: pic3;
   }
   &__second {
      display: grid;
      grid-template-rows: repeat(2, auto);
      gap: 30px;
   }
}

@media (max-width: 1200px) {
   .comands {
      &__wrapper {
         flex-wrap: wrap;
         justify-content: center;
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .comands {
      padding: 30px 0;
      &__third {
         grid-template-areas: "pic1"
                              "pic2"
                              "pic3";
      }
      &__pic1 {
         display: flex;
         justify-content: center;
      }
      &__pic2 {
         display: flex;
         justify-content: center;
      }
      &__pic3 {
         display: flex;
         justify-content: center;
      }
   }
}

