.mini-contacts {
   width: 277px;
   background-color: var(--main-color);
   padding: 20px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   .header__phone__number-link {
      color: var(--light-color);
   }
   .header__phone__number-text {
      color: rgba(255, 255, 255, 0.6);
   }
   .header__phone__number {
      margin-right: auto;
      margin-left: auto;
   }
   &__mails {
      margin-right: auto;
      margin-left: auto;
      text-align: center;
   }
   &__adr {
      margin-right: auto;
      margin-left: auto;
      text-align: center;
   }
   &-link {
      font-family: 'Gilroy';
      font-weight: 600;
      font-size: 20px;
      line-height: 130%;
      color: var(--light-color);
      transition: all 0.3s;
      &:hover {
         text-decoration: underline;
      }
   }
   hr {
      margin: 15px 0;
      width: 237px;
      border: none;
      border-bottom: 1px solid #223545;
   }
   &__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 237px;
      height: 58px;
      background: var(--green-color);
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--light-color);
      margin-bottom: 15px;
      transition: all 0.3s;
      &:hover {
         background-color: var(--light-color);
         color: var(--green-color);
         border: 2px solid var(--green-color);
      }
   }
}