.documents {
   padding: 80px 0;
   h2.title {
      max-width: 584px;
      margin-left: auto;
      margin-right: auto;
      text-align: center;
   }
   &__wrapper {
      display: flex;
      gap: 30px;
   }
   &__item {
      &.active {
         .documents__item-list {
            display: flex;
         }
         .documents__item-name span {
            transform: rotate(180deg);
         }
      }
      &-name {
         width: 585px;
         padding: 14px 20px;
         border: 1px solid #E8E8E8;
         font-family: 'Gilroy';
         font-weight: 700;
         font-size: 20px;
         line-height: 130%;
         color: var(--black-color);
         display: flex;
         justify-content: space-between;
      }
      &-list {
         list-style-type: none;
         margin: 0;
         padding: 25px 21px;
         border: 1px solid #E8E8E8;
         display: flex;
         flex-direction: column;
         gap: 12px;
         display: none;
      }
      &-items {
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: rgba(48, 48, 48, 0.7);
         position: relative;
         padding-left: 24px;
         &::after {
            content: '';
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: var(--green-color);
         }
      }
   }
}

@media (max-width: 1200px) {
   .documents {
      &__wrapper {
         flex-direction: column;
         align-items: center;
      }
   }
}

