.specialists {
   padding: 80px 0;
   &__text {
      margin: 0;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 32px;
      line-height: 130%;
      text-align: center;
      text-transform: uppercase;
      color: var(--black-color);
   }
   &__title {
      font-family: 'Gilroy';
      font-weight: 700;
      font-size: 53px;
      line-height: 130%;
      text-align: center;
      text-transform: uppercase;
      color: var(--black-color);
      margin: 0;
   }
   &__map {
      position: relative;
      margin-top: 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      &__wrapper {
         position: absolute;
         bottom: -122px;
         display: flex;
         gap: 3px;
         width: 100%;
         &-item {
            background-color: var(--green-color);
            display: flex;
            align-items: center;
            flex-direction: column;
            padding: 0px 83px;
            padding-bottom: 25px;
            &-number {
               font-family: 'Gilroy';
               font-weight: 900;
               font-size: 96px;
               line-height: 130%;
               text-align: center;
               color: var(--light-color);
            }
            &-descr {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 16px;
               line-height: 140%;
               text-align: center;
               color: var(--light-color);
            }
         }
      }
   }
}

@media (max-width: 1200px) {
   .specialists {
      &__text {
         font-size: 24px;
      }
      &__title {
         font-size: 36px;
      }
      &__map {
         &__wrapper {
            &-item {
               padding: 0 60px;
               &-number {
                  font-size: 53px;
               }
               &-descr {
                  font-size: 14px;
               }
            }
         }
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .specialists {
      &__map {
         &__wrapper {
            &-item {
               padding: 0 30px;
            }
         }
      }
   }
}

@media (max-width: 576px) {
   .specialists {
      &__map {
         &__wrapper {
            &-item {
               padding: 0 20px;
               &-number {
                  font-size: 36px;
               }
               &-descr {
                  font-size: 12px;
               }
            }
         }
      }
   }
}

