.hero-contacts {
   padding-top: 200px;
   padding-bottom: 80px;
   h2.title {
      max-width: 460px;
      margin-bottom: 15px;
   }
   &__text {
      margin: 0;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--black-color);
   }
   &__items {
      margin-top: 40px;
      display: flex;
      flex-direction: column;
   }
   &-item {
      display: flex;
      flex-direction: column;
      margin-bottom: 49px;
   }
   &__order {
      font-family: 'Gilroy';
      font-weight: 500;
      font-size: 12px;
      line-height: 140%;
      text-decoration-line: underline;
      color: var(--green-color);
      &:hover {
         color:var(--black-color);
         text-decoration: none;
      }
   }
   &__links {
      display: flex;
      max-width: 857px;
      justify-content: space-between;
      &-name {
         font-family: 'Gilroy';
         font-weight: 600;
         font-size: 20px;
         line-height: 130%;
         color: var(--black-color);
         margin-bottom: 8px;
      }
      &-link {
         font-family: 'Gilroy';
         font-style: normal;
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: var(--black-color);
         margin-bottom: 8px;
         &:hover {
            color: var(--green-color);
            text-decoration: underline;
         }
      }
   }
   &__descr {
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--black-color);
      margin-top: 60px;
      margin-bottom: 20px;
   }
}

@media (max-width: 1200px) {
.hero-contacts {
   &__map {
      width: 100%;
      iframe {
         width: 100%;
      }
   }
}
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .hero-contacts {
      &__links {
         flex-direction: column;
         margin-bottom: 15px;
      }
      &-item {
         margin-bottom: 10px;
      }
   }
}

