.application {
   padding-top: 40px;
   padding-bottom: 32px;
   &__wrapper {
      background: url(../img/application.png) center center/cover no-repeat;
      padding: 50px;
   }
   &__text {
      max-width: 463px;
      font-family: 'Gilroy';
      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      color: var(--main-color);
   }
   &__form {
      display: flex;
      align-items: center;
      gap: 20px;
      &-text {
         max-width: 327px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 12px;
         line-height: 140%;
         color: var(--main-color);
         margin-left: 35px;
         a {
            color: var(--main-color);
            border-bottom: 1px solid var(--main-color);
         }
      }
      &__btn {
         width: 213px;
         height: 58px;
         background-color: var(--green-color);
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--light-color);
         border: none;
         cursor: pointer;
         transition: all 0.3s;
         &:hover {
            background-color: var(--light-color);
            color: var(--green-color);
            border: 2px solid var(--green-color);
         }
      }
      input {
         width: 220px;
         height: 58px;
         border: 1px solid #E8E8E8;
         padding-left: 24px;
         &::placeholder {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            color: var(--main-color);
         }
      }
      &-checkbox {
         position: relative;
         input {
            width: 24px;
            height: 24px;
            opacity: 0;
            &:checked {
               .application__form-checkbox label::after {
                  display: block;
               }
            }
         }
         label {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            width: 24px;
            height: 24px;
            background-color: var(--green-color);
            cursor: pointer;
            &::after {
               content: url(../img/svg/applicationArrow.svg);
               position: absolute;
               top: 50%;
               left: 50%;
               transform: translate(-50%, -50%);
               overflow: hidden;
            }
         }
      }
   }
}

.check {
   &.active {
      &::after {
         width: 0;
         height: 0;
      }
   }
}

@media (max-width: 1200px) {
   .application {
      &__form {
         flex-wrap: wrap;
      }
   }
}

