.add-comments {
   padding: 80px 0;
   &__form {
      &__wrapper {
         display: flex;
         gap: 30px;
      }
      &__item {
         display: flex;
         flex-direction: column;
         gap: 25px;
         input {
            background: var(--light-color);
            border: 1px solid #E8E8E8;
            padding: 19px 24px;
            width: 585px;
            &::placeholder {
               font-family: 'Gilroy';
               font-weight: 400;
               font-size: 16px;
               line-height: 130%;
               color: var(--main-color);
            }
         }
         &-rigth {
            input {
               padding: 19px 24px 101px;
            }
            textarea {
               display: block;
               white-space: normal;
               overflow: auto;
               resize: none;
            }
         }
      }
      &__btn {
         background-color: var(--green-color);
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 130%;
         color: var(--light-color);
         border: none;
         padding: 19px 0;
         transition: all 0.3s;
         &:hover {
            background-color: var(--light-color);
            color: var(--green-color);
            border: 2px solid var(--green-color);
         }
      }
   }
}

@media (max-width: 1200px) {
   .add-comments {
      &__form__wrapper {
         flex-direction: column-reverse;
      }
      &__form {
         &__btn {
            width: 585px;
            height: 58px;
         }
      }
   }
}

@media (max-width: 992px) {
   
}

@media (max-width: 767px) {
   .add-comments {
      &__form {
         input {
            width: 100%;
         }
         &__btn {
            width: 100%;
         }
      }
      
   }
}

