.right {
   padding: 80px 0;
   h2.title {
      max-width: 584px;
   }
   &__wrapper {
      display: flex;
      gap: 25px;
   }
   &__item {
      &-text {
         max-width: 585px;
         font-family: 'Gilroy';
         font-weight: 400;
         font-size: 16px;
         line-height: 140%;
         color: rgba(48, 48, 48, 0.7);
         margin: 0;
         margin-bottom: 25px;
      }
      &__list {
         list-style-type: none;
         margin: 0;
         padding: 0;
         display: flex;
         flex-direction: column;
         gap: 10px;
         &-title {
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: rgba(48, 48, 48, 0.7);
            margin-bottom: 25px;
         }
         &-item {
            max-width: 671px;
            font-family: 'Gilroy';
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: rgba(48, 48, 48, 0.7);
            position: relative;
            padding-left: 24px;
            &::before {
               content: '';
               position: absolute;
               top: 50%;
               left: 0;
               transform: translateY(-50%);
               height: 8px;
               width: 8px;
               border-radius: 50%;
               background-color: var(--green-color);
            }
         }
      }
   }
}

@media (max-width: 992px) {
   .right {
      &__wrapper {
         flex-wrap: wrap;
      }
   }
}